@import url(https://fonts.googleapis.com/css?family=Chewy);
@import url(https://fonts.googleapis.com/css?family=Candara);
body,
html,
#root {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  font-family: "Candara";
  width: 100%;
  height: 100%;
  /* flex: 1; */

  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Extra extra small devices (Like galaxy fold 300px and below) */
@media all and (max-width: 300px) {
  body,
  html,
  #root {
    font-size: 11px;
  }
}

/* Extra small devices (phones, between 400px and 600px) */
@media all and (min-width: 300px) and (max-width: 600px) {
  body,
  html,
  #root {
    font-size: 12px;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media all and (min-width: 600px) {
  body,
  html,
  #root {
    font-size: 13px;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media all and (min-width: 768px) {
  body,
  html,
  #root {
    font-size: 14px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media all and (min-width: 992px) {
  body,
  html,
  #root {
    font-size: 15px;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media all and (min-width: 1200px) {
  body,
  html,
  #root {
    font-size: 14px;
  }
}

@media all and (min-width: 1600px) {
  body,
  html,
  #root {
    font-size: 17px;
  }
}



.navbar-style {
  background-color: maroon;
}

.navbar-brand {
  color: white;
  font-size: 28px;
  font-family: Chewy;
}

.container-style {
  padding-top: 20px;
  border-bottom: 2px solid silver;
}

.project-container-style {
  padding-top: 20px;
  padding-bottom: 60px;
}

.header-text {
  font-family: Chewy;
}

.header-text-lg {
  font-family: Chewy;
  font-size: 3rem;
}

.row-padding {
  padding: 16px;
}

.tech-stack-row {
  margin: 1.4rem;
}

.text-style {
  font-size: 1.2rem;
}

.text-style-lg-italic {
  font-size: 1.3rem;
  font-style: italic;
}

.text-style-header-JS {
  font-size: 1.8rem;
  background: -webkit-linear-gradient(yellow, gray);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-style-header-Py {
  font-size: 1.8rem;
  background: -webkit-linear-gradient(yellow, blue);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.text-style-header-AWS {
  font-size: 1.8rem;
  background: -webkit-linear-gradient(#fa7a08, gray);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.project-outer-card {
  border: none;
}

.project-inner-card {
  border-radius: 1.2rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 16px;
  transition: 0.3s;
  overflow: hidden;
}

.project-inner-card:hover {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

.project-card-img {
  width: 12rem;
  height: 12rem;
  -webkit-align-self: center;
          align-self: center;
}

.project-card-title {
  font-weight: bold;
  font-size: 1.2rem;
}

.project-inner-card:hover .project-card-title {
  color: maroon;
}

.project-card-body {
  border-top: 0.2px solid silver;
}

.project-card-text {
  font-size: 1rem;
  padding-bottom: 10px;
}

.project-github-row {
  position: absolute;
  bottom: 0.4rem;
  -webkit-flex: 1 1;
          flex: 1 1;
  width: 100%;
}

.project-card-github {
  color: gray;
  margin-left: 10px;
  cursor: pointer;
}

